.navbar-brand {
    float: left;
    padding: 0 20px;
    font-size: 14px;
    border-right: 1px #909090 solid;
}

.bg-custom{
    background-color:black;
}

li {
    display: inline;
}

a *{
    // height: 100%;
    text-decoration: none;
    display: inline-block;
    color: black;
}

a:hover *{
    color: gray;
}

.postcards{
    width: 100%;
}

.postcard-container{
    width: 100%;
    min-height: 160px;
    margin-top: 10px;
    margin-bottom: 10px;
    transition: transform .1s;
}

.postcard-container:hover{
    transform: scale(1.1);
}

.postcard-text{
    text-decoration: none;
}

.postcard-image{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    display:block;
}

.postcard-image-img{
    margin:-5% 0;
    object-fit: cover;
}

.postcard-text-title{
    font-size: calc(20px + 0.52vw);
    font-family: 'Open Sans', sans-serif;
    font-weight: bold;
}

.postcard-text-summary{
    font-size: calc(13px + 0.35vw);
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
}

.gradient-overlay{
    height: inherit;
    background: linear-gradient(90deg, rgba(255,255,255,0) 25%, rgba(255,255,255,1) 100%);
    background-size: cover;
}

.main-banner{
    
    // color: white;
