.center {
    margin: auto;
    // top: 50%;
    // left: 50%;
    // -ms-transform: translate(-50%, -50%);
    // transform: translate(-50%, -50%);
  }

.post-banner{
    max-width: 100%;
    height: 500px;
    background-size: 100% auto;
    background-position: 0 50%;
    background-repeat: no-repeat;
    object-fit: cover;
}

.section-title{ 
    font-family: 'Bebas Neue', cursive;
    margin-top: 5%;
    margin-bottom: 5%;
}

.img-pp {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: calc(100px + 7.8vw);
    height: calc(100px + 7.8vw);
    object-fit: cover;
}

.rounded-circle {
    border-radius: 50%!important;
}